import {Card, CardContent, CardActions,CssBaseline, Container ,Button, Typography} from '@material-ui/core';
import React , {useEffect, useState} from 'react';

function App() {
  const [jokes,setJokes] = useState([])
  const [cat , setCategory] = useState([])
   

  useEffect(() => {
    // fetch('https://api.chucknorris.io/jokes/random')
      fetch('https://api.chucknorris.io/jokes/random')
      .then((res) => res.json())
      .then((data) => {
        // console.log(data)
         
         
        setJokes((s) => [...s, data.value])
        setCategory((c) => [...c, data.categories])
        console.log(cat)
        
      }, [])
      .catch((err) => console.log(err));
       
  }) 
  console.log(jokes)
  return (
    <div className="App">
      <CssBaseline />
      <Container>
      <Typography variant = "h1" align = "center" >
         Some jokes
         </Typography>

              {cat.map((category, index)=> (
                <h1 key = {index}>{category}</h1>

              ))}


               {jokes.map((joke, index)=> (
                  <Card key = {jokes.id}>
                  <CardContent>
                    <Typography>
                <li key = {index}>{joke }</li>
               </Typography>
               </CardContent>
                <CardActions>
                 <Button variant = "contained" color = "primary"> category </Button>
               </CardActions>
           </Card>
           ))}
      </Container>
      
    </div>
  );
}

export default App;
